.header {
  width: 100%;
  height: 770px;
  background: url("../app/img/bg/bg.png") 0 0/cover no-repeat;
  padding: 30px 0;
  margin-bottom: 100px;
  background-position: center;
}
.header .hero {
  position: relative;
  width: 100%;
  height: 100%;
  color: #ffffff;
}
.header .hero .hero_bg {
  position: absolute;
  width: 100%;
  object-fit: none;
}
.header .hero .hero_container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header .hero .hero_container .hero_main-text h1 {
  font-family: "Bebas Neue", cursive;
  font-size: 60px;
  font-weight: 400;
  line-height: 70px;
  letter-spacing: 5.70937px;
  text-align: center;
}
.header .hero .hero_container .hero_main-text p {
  font-size: 16px;
  line-height: 19px;
  text-align: center;
}
.header .hero .hero_container .hero_scroll-down {
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  letter-spacing: 3.7px;
  cursor: pointer;
}
.header .hero .hero_container .hero_scroll-down span:hover {
  color: #00A4E5;
  transition: 0.3s ease;
}
.header .header_nav {
  margin-top: 30px;
  height: 65px;
  width: 100%;
  border-bottom: 1px solid #E6E6E6;
}
.header .header_nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header .header_nav .container .logo {
  width: fit-content;
}
.header .header_nav .container .logo object {
  pointer-events: none;
  width: 100px;
  height: 20px;
}
.header .header_nav .container .nav_items ul {
  display: flex;
  gap: 3em;
}
.header .header_nav .container .nav_items ul li {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1.09091px;
  cursor: pointer;
}
.header .header_nav .container .nav_items ul li:hover {
  color: #00A4E5;
  transition: 0.3s ease;
}